:root{
    --teal-gradient-opaque:linear-gradient(135deg, rgba(2, 170, 176, .55), rgba(0, 205, 172, .55));
    --drop-shadow-card:drop-shadow(8px 8px 4px rgba(0, 0, 0, .6));
    --box-shadow-card:(10px 10px 20px 1px rgba(0,0,0,.4));
  }

.header {
    perspective: 150rem;
    -moz-perspective: 150rem;
    position:relative;
    transition: all 0.8s ease-in-out;
    width:92vw;
    height:10rem;
    margin: 1rem;
    border-radius: 3px;
    backface-visibility: hidden;
    display: inline-block;
    z-index: 1;
    
  }
  
  .header__front{
    font-size: 2rem;
    position: absolute;
    /* width: 100%; */
    /* overflow: hidden; */
    margin: 0rem;
    box-shadow: var(--box-shadow-card);
    background: var(--teal-gradient-opaque);
    filter: var(--drop-shadow-card);
  }

  .header__back{
    transform: rotateX(180deg);
    font-size: 1rem;
    text-align:left ;
    padding: 1rem;
    color:white;
    margin: 0rem;
    box-shadow: var(--box-shadow-card);
    background: var(--teal-gradient-opaque);
    filter: var(--drop-shadow-card);

  }
  

  .header:hover .header__front{
    transform: rotateX(-180deg);
  }

  .header:hover .header__back{
    transform: rotateX(0);
  }

  @media only screen 
  and (min-device-width : 320px) 
  and (max-device-width : 480px) {

    .header__front{
        font-size: 1.5rem!important;
    }
  }