video { object-fit: fill; }

.video {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    background: rgba(0,0,0,0.5);
    opacity: 0.6;
}

@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
  video{
    background-size: cover;
  }
}